import './App.css';
import { eventWrapper } from '@testing-library/user-event/dist/utils';
import React, {useState} from 'react';
import PizZip from 'pizzip';
import { DOMParser } from '@xmldom/xmldom';

const wordMinimum = 10;

const App = () => {
	const [inputType, setInputType] = useState('text');
	const [text, setText] = useState('');
	const [file, setFile] = useState(null);
	// const [content, setContent] = useState('');
	const [showContent, setShowContent] = useState(false);
	const [paragraphs, setParagraphs] = useState([]);

	const str2xml = (str) => {
		if(str.charCodeAt(0) === 65279){
			str = str.substr(1);
		}
		return new DOMParser().parseFromString(str, "text/xml");
	};

	const getParagraphs = (content) => {
		const zip = new PizZip(content);
		const xml = str2xml(zip.files["word/document.xml"].asText());
		const paragraphsXml = xml.getElementsByTagName("w:p");
		const paragraphs = [];
		for (let i = 0, len = paragraphsXml.length; i < len; i++){
			let fullText = '';
			const textsXml = paragraphsXml[i].getElementsByTagName("w:t");
			for (let j = 0, len2 = textsXml.length; j < len2; j++){
				const textXml = textsXml[j];
				if (textXml.childNodes){
					fullText += textXml.childNodes[0].nodeValue;
				}
			}
			if(fullText){
				paragraphs.push(fullText);
			}
		}
		return paragraphs;
	};

	const handleInputChange = (event) => {
		if (event.target.value === 'file'){
			setInputType('file');
		}else{
			setInputType('text');
		}
		console.log(event.target);
	};

	const handleTextChange = (event) => {
		// setContent(event.target.value)
		setText(event.target.value);
		const paragraphs = event.target.value.split('\n');
		console.log('paragraphs', paragraphs);
		setParagraphs(paragraphs);
	};

	const handleFileChange = (event) => {
		const selectedFile = event.target.files[0];
		setFile(selectedFile);
	};

	const handleFileRead = (event) => {
		const fileContent = event.target.result;
		const paragraphs = getParagraphs(fileContent);
		console.log('paragraphs', paragraphs);
		setParagraphs(paragraphs);
		setShowContent(true);
	};

	const handleSubmit = () => {
		if (inputType === 'file'){
			console.log('upload file', file);
		}else{
			console.log('input text', text);
		}
		setShowContent(true);
		if (inputType === 'file' && file){
			const reader = new FileReader();
			reader.onload = handleFileRead;
			reader.readAsBinaryString(file);
			// reader.readAsText(file);
		}else{
			setShowContent(true);
		}
	};

	const handleClear = () => {
		setInputType('file');
		setText('');
		// setContent('');
		setParagraphs([]);
		setFile(null);
		setShowContent(false);
	};

	return (
		<div className='Aigc-detector-header'>
			<header>
 				<title>AI Content Detection</title>
 			</header>
			<body className='My-body'>
				<h1 className='My-header'>TextTrace: An AI Content Detector</h1>
				<div className='Main-box'>
					{!showContent && (
						<div className='Box1'>
							<h3 className='flex-item'>Upload file or input text</h3>
							<div className='flex-item My-radio'>
								<input
									type='radio'
									value='file'
									checked={inputType === 'file'}
									onChange={handleInputChange}
								/>
								<label>
									Upload file
								</label>

								<input
									type='radio'
									value='text'
									checked={inputType === 'text'}
									onChange={handleInputChange}
								/>
								<label>
									Input text
								</label>
							</div>
							{inputType === 'file' ? (
								<div>
									<input className='Input-file' type='file' accept='.docx' onChange={handleFileChange} />
								</div>
								) : (
								<div className='Text-block'>
									<textarea className='Text-area' value={text} onChange={handleTextChange} />
								</div>
							)}
							<button className='My-button' onClick={handleSubmit}>Submit</button>
						</div>
					)}
					{showContent && (
						<div className='Box1'>
							<br></br>
							<h4 className='Text-content'> Text Content: </h4>
							{/* <span>Note: only paragraphs with at least 10 words will be</span> */}
							{paragraphs.filter(text => text.split(' ').length > wordMinimum)
									   .map((text, index) => {
										const confidence = Math.floor(Math.random()*50)+51;
										const isAI = text.trim().startsWith('In addition to the impact') ||
													text.trim().startsWith('The hypothesis that rapid and repeated') ||
													text.trim().startsWith('Recent studies have found that anti-inflammatory') ||
													text.trim().startsWith('Diet plays a significant role in determining');
										const models = ['Chatgpt3.5', 'Google Bard AI', 'BAI AI', 'Perplexity AI'];
										const rand_num = Math.floor(Math.random() * 4);
										return isAI ? (
											<div>
												<span className='AI-warn'>
													The following paragraph is likely to be AI-generated by {models[rand_num]} with confidence {confidence}%.
												</span>
												<p className='AI-para'>
													{text}
												</p>
											</div>
											) : (
											<p>
												{text}
											</p>
										)
								// <p 	key={index}
								// 	style={text.trim().endsWith('(chatgpt)') ||
								// 		text.trim().endsWith('(Google Bard AI)') ||
								// 		text.trim().endsWith('(Prexlexity AI)') ||
								// 		text.trim().endsWith('(Bai AI)') ?
								// 		{backgroundColor: 'yellow'} : {}}>
								//  	{`${text.trim().endsWith('(chatgpt)') ||
								// 		text.trim().endsWith('(Google Bard AI)') ||
								// 		text.trim().endsWith('(Prexlexity AI)') ||
								// 		text.trim().endsWith('(Bai AI)') ? '1' : ''}${text}`}
								// </p>
								})}
							<button className='My-button' onClick={handleClear}>Return</button>
						</div>
					)}
				</div>
			</body>
		</div>
	);
};
export default App;
